/* GLOBAL CSS */
@import url('https://fonts.googleapis.com/css2?family=Courgette&display=swap');
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.1.1/css/all.min.css');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    transition: 0.2s linear;
    font-family: 'Courgette', cursive;
}

button{
    padding: 12px;
    border-radius: 5px;
    border: none;
    cursor: pointer;
}

#login{
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
}

#login > button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 120px;
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

#login > button:hover{
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

#login > button p {
    margin-left: 8px;
    font-weight: bold;
    font-size: 16px;
}

#login > button > img{
    width: 30px;
}

#Navbar{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0px 10px;
    flex-wrap: wrap;
}

#Navbar div{
    display: flex;
    align-items: center;
}

#Navbar button{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    font-weight: bold;
    font-size: 15px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

#Navbar button:hover{
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

#Navbar div img{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin: 5px;
}

.fa-door-open{
    margin-right: 10px;
    font-size: 20px;
}

#qrcode{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: calc(100vh - 50px);
}

#qrcode > img{
    height: 300px;
    width: 300px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
    border-radius: 5px;
}

